import React,{Component} from 'react';
import Input from '../../components/Input';

class COM_Input extends Component{
  getValue(e){
    console.log(e.target.value);
  }
  render(){
    return(
      <div style={{padding:"6px"}}>
          <h4 style={{padding:'8px 0',margin:'0'}}></h4>
          <Input placeholder="" onBlur={this.getValue.bind(this)} /> 

          <h4 style={{padding:'8px 0',margin:'0'}}></h4>
          <Input disabled placeholder="" /> 

          <h4 style={{padding:'8px 0',margin:'0'}}></h4>
          <Input type="textarea" placeholder="" autoSize={true} />

          <h4 style={{padding:'8px 0',margin:'0'}}>rows </h4> 
          <Input type="textarea" placeholder="" maxlength={40} rows={10} /> 

          <h4 style={{padding:'8px 0',margin:'0'}}>large </h4>
          <Input placeholder="" size="large" /> 

          <h4 style={{padding:'8px 0',margin:'0'}}>small </h4>
          <Input placeholder="" size="small" /> 
          
          <h4 style={{padding:'8px 0',margin:'0'}}>mini </h4>
          <Input placeholder="" size="mini" /> 
      </div>
    )
  }
}

export default COM_Input;


