import React, { Component } from 'react';
//button  
import {Button} from 'rc-react';
import './button.less';
class COM_Button extends Component{
  render(){
    return(
      <div className="rc-button">
      <h3></h3>
       <div className="main-button">
          <div style={{margin:'10px'}}><Button class="btn"></Button></div>
          <div style={{margin:'10px'}}><Button class="btn btn-disabled" disabled></Button></div>
          <div style={{margin:'10px'}}><Button class="btn btn-success"></Button></div>
          <div style={{margin:'10px'}}><Button class="btn btn-warning"></Button></div>
          <div style={{margin:'10px'}}><Button class="btn btn-danger"></Button></div>
        </div>
        <h3></h3>
        <div className="main-button">
          <div style={{margin:'10px'}}><Button class="btn" icon="left"></Button></div>
          <div style={{margin:'10px'}}><Button class="btn btn-success" icon="notification"></Button></div>
          <div style={{margin:'10px'}}><Button class="btn" icon="phone"></Button></div>
          <div style={{margin:'10px'}}><Button class="btn btn-danger" icon="frown"></Button></div>
        </div>
      </div>
    )
  }
}

export default COM_Button;


